.keyboard-body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* height: 70vh; */
}

.title {
  color: mintcream;
  text-transform: uppercase;
  margin-top: 3em;
  margin-bottom: 3em;
  font-size: 1em;
  letter-spacing: 0.3em;
}

.keyboard {
  display: flex;
  flex-direction: column;
  border: 5px solid #e3e0e0;
  border-radius: 1rem;
  background-color: #393939;
}

.row {
  list-style: none;
  display: flex;
  padding: 0;
}

.keyboard ul {
  margin: 0.1rem;
}

.keyboard li {
  height: 3em;
  width: 3em;
  color: rgba(0, 0, 0, 0.7);
  border-radius: 0.4em;
  line-height: 3em;
  letter-spacing: 1px;
  margin: 0.3em;
  transition: 0.05s;
  text-align: center;
  font-size: 1em;
}

.key {
  transition: all 0.1s;
}

#Tab {
  width: 5em;
}

#CapsLock {
  width: 6em;
}

#ShiftLeft {
  width: 8em;
}

#Enter {
  width: 5.8em;
}

#ShiftRight {
  width: 7.6em;
}

#Backspace {
  width: 5em;
}

#AltLeft {
  width: 5.4rem;
}

#MetaLeft {
  width: 3.5rem;
}

#Space {
  width: 17.5rem;
}

#AltRight {
  display: flex;
  align-items: center;
  justify-content: center;
}

#carets {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

#ArrowUp,
#ArrowDown {
  height: 42%;
  border-radius: 3px;
}

#ArrowLeft,
#ArrowRight {
  display: flex;
  align-items: center;
  justify-content: center;
}

.orange {
  border: 2px solid #f29d4d;
  background-color: #f29d4d;
}

.dark-gray {
  border: 2px solid #767a7d;
  background-color: #767a7d;
}

.white {
  background-color: #f3f5f5;
  border: 2px solid #f3f5f5;
}

.fill-out-key {
  background-color: slategrey;
  border: 2px solid slategrey;
}

.selected {
  background-color: transparent;
  -webkit-animation: vibrate-1 0.3s linear infinite both;
  animation: vibrate-1 0.3s linear infinite both;
}

